/* base styles */
html,
body {
  display: flex;
  flex-direction: column;
  height: 100%;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

main {
  flex: 1 0 auto;
}

header {
  flex: 0 0 auto;
}

img {
  display: block;
  height: auto;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  font-style: normal;
}

button {
  cursor: pointer;
}

/* your css code */

.container {
  width: 320px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}
